import LC from 'leancloud-storage';  // 导入 leancloud 数据存储模块


// const TABLE_NAME = 'Category';  // 存储数据的表名(集合)
class DataService {
    constructor(table_name) {
        this.TABLE_NAME = table_name;
    }
   /**
    * 获取所有数据
    * @param {*} include 包含的其他 Class 名称，例如：["category", "createdBy", "lastEditor"]
    * @param {*} field 排序，列如：createdAt Desc
    * @returns 
    */
    fetchAll(include = [''], field = 'createdAt') {
        let query = new LC.Query(this.TABLE_NAME);
        return new Promise((resolve, reject) => {
            //  include() 用于读取存储在其他表中的数据， 类似于 MySQL 的 join 查询
            query.include(include)
                // 降序排序 
                .descending(field)
                .find()
                .then(resp => {
                    console.log('fetchAll():', resp);
                    resolve(resp);
                })
                .catch(error => {
                    console.log('fetchAll():', error);
                    reject(error);
                });
        });
    }

    /**
     * 添加新记录
     * @param {Object} data : 以对象形式提供的数据 
     * @returns 创建成功的对象
     */
    create(data) {
        let Collection = LC.Object.extend(this.TABLE_NAME);
        let query = new Collection(data);
        return new Promise((resolve, reject) => {

            query.save()
                .then(resp => {
                    console.log('create(data):', resp);
                    resolve(resp);
                })
                .catch(error => {
                    console.log('create(data):', error);
                    reject(error);
                });
        });
    }

    /**
     * 
     * @param {*} id 要查找的 id 值 
     * @returns 找到的记录
     */
    getDataById(id) {
        return new Promise((resolve, reject) => {
            let query = new LC.Query(this.TABLE_NAME);
            query.get(id)
                .then(resp => {
                    console.log('getDataById(id): ', resp);
                    resolve(resp);
                })
                .catch(error => {
                    console.log('getDataById(id): ', error);
                    reject(error);
                });
        });
    }

    /**
     * 删除一条记录
     * @param {*} id 要删除记录的 id
     * @returns 
     */
    delete(id) {
        console.log('delete by Id:', id);
        return new Promise((resolve, reject) => {
            let query = LC.Object.createWithoutData(this.TABLE_NAME, id);
            query.destroy()
                .then(resp => {
                    console.log('delete(id):', resp);
                    resolve(resp);
                })
                .catch(error => {
                    console.log('delete(id):', error);
                    reject(error);
                });
        });
    }

    /**
     * 修改数据
     * @param {*} id 要修改的记录的 id
     * @param {*} data 包含除 id 以外 其他字段组成的数据, 对象类型
     * @returns 更新后的记录
     */
    update(id, data) {
        console.log('update data:', data);
        return new Promise((resolve, reject) => {
            // 通过 id 匹配要修改的记录
            let query = LC.Object.createWithoutData(this.TABLE_NAME, id);
            query.set(data);  // 修改数据
            query.save()
                .then(resp => {
                    console.log('update(id, data): ', resp);
                    resolve(resp);
                })
                .catch(error => {
                    console.log('update(id, data): ', error);
                    reject(error);
                });
        });
    }

    /**
     * 批量删除，先按id 查询出所删除的数据，再调用destroyAll() 进行删除
     * 其他批量操作类似
     * @param {*} items :所有要删除的记录绷的数组
     * @returns 
     */
    deleteAll(items) {
        // 获取各条记录的 id 组成的数组
        let ids = items.map(item => { return item.id; });
        let query = new LC.Query(this.TABLE_NAME);
        return new Promise((resolve, reject) => {
            // 根据多个 id 组成的数组查询符合条件的记录
            query.containedIn('objectId', ids)
                .destroyAll() // 将查询出来的数据批量删除
                .then(resp => {
                    console.log('deleteAll(items): ', resp);
                    resolve(resp);
                })
                .catch(error => {
                    console.log('deleteAll(items): ', error);
                    reject(error);
                });
        });
    }


    /**
     * 根据 id 组成的数组查找多条记录
     * @param {*} ids 多个 id 值 组成的数组
     */
    findItemsByIds(ids) {
        let query = new LC.Query(this.TABLE_NAME);
        return new Promise((resolve, reject) => {
            query.containedIn('objectId', ids)
                .then(resp => {
                    console.log('findItemsByIds(ids): ', resp);
                    resolve(resp);
                })
                .catch(error => {
                    console.log('findItemsByIds(ids): ', error);
                    reject(error);
                });
        });
    }


    /**
     * 批量更新数据
     * @param {*} items :要更新的数据集合
     * @param {*} column :要新的列名
     * @param {*} val : 更新的值
     * @returns 更新后的数据集合
     */
    batchUpdate(items, column, val) {
        let ids = items.map(item => { return item.id; });
        let query = new LC.Query(this.TABLE_NAME);
        return new Promise((resolve, reject) => {
            query.containedIn('objectId', ids)
                .find()
                .then(resp => {
                    console.log(' batchUpdate(items, column, val): ', resp);
                    return resp.map(item => {
                        item.set(column, val);  // 遍历并修改 指定的 字段
                        console.log(item);
                        return item;
                    });
                })
                .then(result => {
                    // 批量更新数据
                    LC.Object.saveAll(result)
                        .then(level => {
                            resolve(level);
                        });
                })
                .catch(error => {
                    console.log('updateALL(items): ', error);
                    reject(error);
                });
        });
    }


    /**
     * 是否已经有相应标题
     * @param {*} title ：要查找的标题
     * @returns true 或 false
     */
    exists(title) {
        let query = new LC.Query(this.TABLE_NAME);
        return new Promise((resolve, reject) => {
            query.equalTo('title', title);
            query.count()
                .then(count => {
                    console.log('exists(title):', count);
                    if (count) {
                        resolve(true);
                    } else {
                        resolve(false);
                    }
                })
                .catch(error => {
                    console.log('exists(title):', error.code);
                    if (error.code == '101') {
                        resolve(false);
                    } else {
                        reject(false);
                    }
                });
        });
    }

    // 获取当前已登录的用户
    currentUser() {
        return LC.User.current();
    }



}

// 基类导出时不能实例化
export default DataService;