<template>
  <v-container fluid>
     <v-divider />
    <v-row class="board">
      <v-col cols="12" md="6">
        <v-list>
          <v-list-item :to="`/board/${board.id}`">
            <v-list-item-title>
              <span class="text-h6">
                {{ board.title }}
              </span>
            </v-list-item-title>
          </v-list-item>
        </v-list>
        <span class="grey--text text--darken-1 ml-4">
          {{ board.description }}
        </span>
      </v-col>
      <v-col cols="6" class="d-none d-md-flex">
        <v-row>
          <v-col cols="12">
            <span v-if="topics.length == 0" class="grey--text">
              当前分类无内容，请添加文章
            </span>
            <v-list>
              <v-list-item
                v-for="(item, i) in topics"
                :key="i"
                :to="`/topicdetail/${item.id}`"
              >
                <v-list-item-title>
                  <span class="mr-5">
                    {{ item.title }}
                  </span>
                  {{ item.createdAt }}
                </v-list-item-title>
              </v-list-item>
            </v-list>
          </v-col>
        </v-row>
      </v-col>
    </v-row>
   
  </v-container>
</template>

<script>
import moment from "moment"; //日期处理类库
import TopicService from "@/service/topic_service.js";

export default {
  name: "topic-board",
  data: () => ({
    item: "",
    topics: [],
    borderColors: ["#25aae2", "#652d90", "#ed207b", "#3ab54a", "#f7941d"],
  }),
  props: ["board"],
  mounted() {
    this.getTopics(this.board.id);
  },
  methods: {
    getTopics(id, limit = 5, skip = 0) {
      TopicService.getTopics(id, limit, skip)
        .then((resp) => {
          console.log("board:", resp);
          this.topics = resp.map((item) => this.formJson(item));
        })

        .catch((error) => {});
    },

    formJson(item) {
      return {
        id: item.id,
        category: item.get("category") ? item.get("category").get("title") : "",
        title: item.get("title"),
        content: item.get("content"),
        level: item.get("level"),
        views: item.get("views"),
        reply: item.get("reply"),
        tags: item.get("tags"),
        createdAt: moment(item.createdAt).format("lll"),
        updatedAt: moment(item.updatedAt).format("lll"),
        createdBy: item.get("createdBy")
          ? this.getUser(item.get("createdBy"))
          : "",
        lastEditor: item.get("lastEditor")
          ? this.getUser(item.get("lastEditor"))
          : "",
      };
    },

    // 获取用户表中各个字段的值
    getUser(user) {
      // console.log(user);
      return {
        id: user.id,
        username: user.get("username"),
        email: user.get("email"),
      };
    },
  },
};
</script>

<style scoped>
.board {
  border-left: 8px solid #25aae2; 
}

</style>